@import url(https://fonts.googleapis.com/css?family=Lato&display=swap);
.header-image {
  width: 100%;
  height: 750px;
  box-shadow: 0 0 2px black, inset 0 0 2px black;
}
.header-text {
  line-height: 1;
  color: white;
  text-transform: uppercase;
  text-align: center;
  font-family: "Open Sans", sans-serif;
  font-size: 150px;
  font-weight: 800;
  top: 50%;
  position: absolute;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  border: 15px solid white;
  text-shadow: 0 0 2px black, 0 0 2px black, 0 0 2px black, 0 0 2px black,
    0 0 2px black, 0 0 2px black, 0 0 2px black, 0 0 2px black, 0 0 2px black,
    0 0 2px black, 0 0 2px black, 0 0 2px black, 0 0 2px black, 0 0 2px black,
    0 0 2px black, 0 0 2px black, 0 0 2px black, 0 0 2px black, 0 0 2px black,
    0 0 2px black;
  box-shadow: 0 0 2px black, 0 0 2px black, 0 0 2px black, inset 0 0 2px black,
    inset 0 0 2px black, inset 0 0 2px black, inset 0 0 2px black,
    inset 0 0 2px black, inset 0 0 2px black, inset 0 0 2px black,
    inset 0 0 2px black;
}

.about-title {
  margin: 100px;
  font-size: 75px;
  font-family: "Lato", sans-serif;
}
.about-body {
  font-size: 40px;
  font-family: "Lato", sans-serif;
  max-width: 75%;
  text-align: center;
  margin: 0 auto;
  margin-bottom: 100px;
}

.image-gallery {
  margin: 0 auto;
  max-width: 75%;
}

